.login-form {
  margin: 20px auto;
  max-width: 400px;
  color: $midGrey;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  .login-form-forgot {
    float: right;
  }
  .login-form-button {
    width: 100%;
  }
  .label {
    color: $midGrey;
  }
}